@font-face {
  font-family: 'flowfont';
  src: url('../fonts/flowfont.eot?33669169');
  src: url('../fonts/flowfont.eot?33669169#iefix') format('embedded-opentype'),
    url('../fonts/flowfont2.woff2?33669169') format('woff2'),
    url('../fonts/flowfont.woff?33669169') format('woff'),
    url('../fonts/flowfont.ttf?33669169') format('truetype'),
    url('../fonts/flowfont2.svg?33669169#flowfont') format('svg');
  font-weight: normal;
  font-style: normal;
}
/* Chrome hack: SVG is rendered more smooth in Windozze. 100% magic, uncomment if you need it. */
/* Note, that will break hinting! In other OS-es font will be not as sharp as it could be */
